import React from 'react';
import ReactDOM from 'react-dom';
import { BrowserRouter as Router } from  'react-router-dom';
import App from './components/App';
import './devBuild/styles/index.css';

ReactDOM.render(
  <Router>
    <App />
  </Router>,
  document.getElementById('root')
);


